import { Value } from '@ali/4ever-cangjie';
import { getClosestParagraph } from '@ali/4ever-utils';

export default function isSelectionInHeading(value: Value, type: string): boolean {
  if (!value) return false;
  const { startBlock } = value;
  if (!startBlock) return false;
  const block = getClosestParagraph(value);
  return !!block && block.type === type;
}

export const isSelectionInOnlyHeading = (value: Value, type: string): boolean => {
  if (!value) return false;
  const { startBlock } = value;
  if (!startBlock) return false;
  const block = getClosestParagraph(value);
  if (!block) return false;
  if (block.data.list || block.data.blockquote || block.data.calloutPr) return false;
  if (block.type !== type) return false;
  return true;
};
